main#main {
    .work-main {
        padding: 40px;

        .work-body {
            // width: calc(100% - var(--work-size));
            // margin-right: var(--work-size);
            // padding:40px;
            margin-right: 40px;
            flex: 1;

            >div {
                margin-left: auto;
                margin-right: auto;
            }

            img.work {
                max-height: calc(90vh - var(--header-height)) !important;
                margin-bottom: 40px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .work-aside {
            width: var(--work-size);

            .aside-block {
                margin-bottom: 40px;

                .aside-block-title {
                    text-transform: uppercase;
                    font: 300 10px var(--font2);
                    color: var(--color2);
                    letter-spacing: 1px;
                    margin-bottom: 20px;
                }
            }




            .user {
                .avatar {
                    border-radius: 50%;
                    margin-right: 10px;
                    size: 50px;
                }

                .user-info {

                    .username {
                        font: 400 17px var(--font2);
                        margin-bottom: 5px;
                    }

                    .status {
                        font: 400 12px var(--font2);
                    }
                }
            }

            .work-buttons {
                >button {
                    width: 50%;

                    &:nth-child(1) {
                        margin-right: 5px;
                    }

                    &:nth-child(2) {
                        margin-left: 5px;
                    }
                }
            }

            .work-info {
                &-title {
                    font: 300 22px var(--font2);
                    margin-bottom: 20px;
                }

                &-description {
                    font: 400 14px/150% var(--font2);
                    color: var(--color7);
                }
            }

            .work-stats {

                li {
                    margin-right: 40px;

                    &:last-child {
                        margin-right: 0;
                    }

                    .value {
                        font: 400 18px var(--font2);
                        text-align: center;

                    }

                    .key {
                        margin-top: 5px;
                        font: 400 10px var(--font2);
                        text-transform: uppercase;
                        color: var(--color7);
                        text-align: center;
                    }
                }
            }

            .work-tags {
                margin: -5px;

                li {
                    background: var(--color4);
                    margin: 5px;
                    padding: 5px 10px;
                    font: 400 12px var(--font2);
                    border: 1px solid var(--color6);

                    &:hover {
                        cursor: pointer;

                        a {
                            color: var(--color5);
                        }
                    }

                    a {
                        color: var(--color2);
                    }
                }
            }

            .work-author {
                li {
                    width: calc(100% / 3 - 2px);
                    margin: 1px;

                    .grid-list-item {
                        width: 100%;
                        height: inherit;
                        display: block;
                        .cover {
                            size: 100%;
                            object-fit: cover;
                            object-position: center;
                        }

                        &:before {
                            content: "";
                            width: 1px;
                            margin-left: -1px;
                            float: left;
                            height: 0;
                            padding-top: 100%;
                        }

                        &:after {
                            content: "";
                            display: table;
                            clear: both;
                        }
                    }
                }

            }
        }
    }
}

footer#footer {
    display: none;
}